body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container, .container-big {
  width: calc(100%-30px);
  max-width: 1024px;
  margin: 0 auto;
}
.container-big {
  max-width: 1250px;
}

/* FLEX */
.flex {
  display: flex;
}

.horizontal, .horizontal-only {
  flex-direction: row;
}
.vertical, .vertical-ho {
  flex-direction: column;
}
@media screen and (max-width: 720px){
  .horizontal {
    flex-direction: column;
  }
  .vertical-ho {
    flex-direction: row;
  }
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }

.wrap { flex-wrap: wrap; }
.flex-basis-40 { flex-basis: 40%; }
@media screen and (max-width: 720px){
  .flex-basis-40 { flex-basis: 100%; }
}

.text-center {
  text-align: center;
}

.form-input {
  padding: 10px 15px;
  border-radius: 10px;
  border: 1px solid #474747;
  width: calc(100% - 40px );
  margin: 5px;
  font-size: 1rem;
}

/* COMPONENTS */
.TopBar {
  background-color: #2884C7;
  height: 5px;
  width: 100%;
}

.NavBar {
  padding: 15px 0;
}
.logo {
  width: 80%;
  max-width: 250px;
  margin: 10px;
}
.form-input[name="pesquisa"] {
  width: 80%;
}

.Categorias {
  background-color: #386ACA;
  color: #fefefe;
}
.Categorias > div > div {
  padding: 15px;
  cursor: pointer;
}
.Categorias > div > div:hover {
  background-color: rgb(120, 155, 221);
}

.HeroShop {
  padding: 15px 0;
}
.foto-min {
  width: 80px;
  height: 80px;
  border: 1px solid rgb(216, 216, 216);
  cursor: pointer;
  margin: 3px;
}
@media screen and (max-width: 720px){
  .foto-min {
    width: 50px;
    height: 50px;
  }
}
.foto-main {
  width: 90%;
}
.padding-10 {
  padding: 10px;
}
.price {
  margin: 0;
  font-size: 2.2rem;
  color: #d62f19;
}
.price-sub, .price>span{
  font-size: 1rem;
  color: #777;
}
.button-cta {
  background-color: #1da00c;
  width: 90%;
  padding: 15px 20px;
  margin: 10px;
  font-size: 1.8rem;
  border: 0;
  border-radius: 10px;
  color: #fefefe;
  cursor: pointer;
}
.button-cta:hover {
  background-color: #126407;
}
.button-action{
  background-color: #a00c0c;
  width: 90%;
  padding: 10px 15px;
  margin: 10px;
  font-size: 1rem;
  border: 0;
  border-radius: 10px;
  color: #fefefe;
  cursor: pointer;
}
.button-action:hover {
  background-color: #6b0808;
}
.hr { width: 100%; }
.hr-80 { width: 80%; }
.hr-gray { color: #999; }

.img-flex {
  width: 90%;
}

.Avisos {
  color: #FEFEFE;
  background-color: rgb(97, 143, 230);
}

.padding-20 {
  padding: 20px;
}
tr > td:first-child {
  background-color: rgb(97, 143, 230);
  color: #fefefe;
}
td {
  padding: 7px;
  background-color: rgb(238, 238, 238);
}

.Recomendacoes {
  padding: 30px 0;
}

.Newsletter {
  padding: 10px;
  background-color: #202020;
  color: #fefefe;
}
.button-newsletter {
  background-color: #386ACA;
  color: #FEFEFE;
  font-size: 1rem;
  border-radius: 10px;
  border: 0;
  padding: 10px 15px;
  cursor: pointer;
}
.button-newsletter:hover {
  background-color: rgb(12, 47, 112);
}

.Avaliacoes {
  padding: 10px;
}

.Loja {
  background-color: #386ACA;
  color: #fefefe;
}
.Loja > div >div {
  padding: 10px;
}
.Loja > div >div:first-child {
  background-color: #2c2b2b;
  color: rgb(255, 255, 255);
}

.Institucional {
  padding: 10px;
}

.Footer {
  padding: 12px;
  background-color: #2c2b2b;
  color: #fefefe;
}